@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap');
/* Variables CSS */
:root{
--first-color: hsl(353, 100%, 65%);
--title-color: hsl(244, 24%, 26%);
--text-color: hsl(244, 16%, 43%);
--bodv-color:hsl(258, 60%, 98%);
--container-color:#fff;

/*Color mode HSL (hue, saturation, lightness)'
Font and Typography .5rem = 8px trem = 16px . . .*/
--body-font: 'Rubik', sans-serif;
--h1-font-size: 2.25rem;
--h2-font-size: 1.5rem;
--h3-font-size:1.25em;
--normal-font-size: 1rem;
--small-font-size: 0.875rem;
--smaller-font-size: 0.813rem;

/* font weight */
--font-medium: 500;
--font-semibold: 600;
--font-bold: 700;
/* Box Shadow  */
--shadow: Opx 5px 20px 0px rgb(69 67 96 / 10%) ;

/* Border Radius */
-border-radius: 20px;

}

/* responsive */
@media screen and (max-width: 1024px) {
  :root{
  --h1-font-size: 1.75rem;
  --h2-font-size: 1.25rem;
  --h3-font-size:1em;
  --normal-font-size: .938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
    }
}


/* base */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

html{
  scroll-behavior: smooth;
}

body,
button,
input,
textarea{
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body{
  border-color: var(--bodv-color);
  line-height: 1.7;
  color: var(--text-color);
}
h1,h2,h3{
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;

}

ul{
  list-style: none;
}
 
a{
  text-decoration: none;
}

button{
  cursor: pointer;
  border: none;
  outline: none;

}
img{
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
/* resuable css classes */

.container{
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin: auto ;
}

.grid{
  display: grid;

}

.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}

.section__title{
  font-size: var(--h1-font-size);
  margin-left: .875rem;
  font-weight: var(--font-bold);
  margin-bottom: 3.75rem;
}

.section_title::before{
  content: '';
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.87 Srem;
  top:-0.875em;
}

.btn{
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  border: 1px solid transparent;
  color: #fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}

 @keyframes button-push {
  50%{
    transform: scale(.8);
  }
  100%{
    transform: scale(1);
  }
  
 }
.btn:hover{
  animation: button-push 0.3s linear 1;
}

